@import url(https://fonts.googleapis.com/css2?family=Signika+Negative&display=swap);
/* @import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap'); */

:root {
  --primary-white : #ffffff;
  --primary-light-grey: #f4f4f4;
  --primary-grey: #ccd1d9;
  --primary-dark-grey: #666666; 
  --primary-blue: #00CACA;
  --primary-dark : #212529;
  --primary-red: #ef4035;
  --primary-hover-red : #f33501;
  --primary-black: #000000;
  --primary-green: green;
  --primary-transparent-black: rgba(0,0,0,00.7);
}

body {
  /* font-family: 'Roboto Condensed' ,sans-serif !important; */
  font-family: 'Signika Negative', sans-serif !important;
}
/************************ NAVBAR ************************/
.nav-wrapper{
  border-bottom: 0.1875rem solid #00CACA;
  border-bottom: 0.1875rem solid var(--primary-blue);
}
nav {
  z-index: 2;
}
.logo {
  width: 100px;
  margin-left: 50px;
}

/******* MENU LINKS *******/
nav a.nav-link {
  font-size: .8rem;
  letter-spacing: 0.0375rem;
  cursor: pointer ;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem  ;
  margin-left: 0.75rem;
  color: #ffffff !important;
  color: var(--primary-white) !important;
  border-top:  0.1875rem solid #212529;
  border-top:  0.1875rem solid var(--primary-dark);
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 6rem !important;
 }

 @media(max-width: 768px) {
  .navbar-brand {
    width: 7rem;
  }
}

.navbar-nav > li > a.active, .navbar-nav > li > a:focus {
  border-top: 0.1875rem solid #00CACA;
  border-top: 0.1875rem solid var(--primary-blue);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #00CACA;
  border-top: 0.1875rem solid var(--primary-blue);
}

/************************ HEADER ************************/

.header-wraper {
  position: relative;
  background: url(/portfolio-alexmo/static/media/ReactWebsite_background.b7866200.jpeg) no-repeat;
  background-size: cover;
  background-position: center ;
  height: 100vh;
}

.main-info {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  z-index: 1;
}

.header-intro{
  font-size: 3rem;
  color: #ffffff;
  color: var(--primary-white);
}

.header-nickname{
  color: #00CACA;
  color: var(--primary-blue);
}

.main-info h1 {
  color : #00CACA;
  color : var(--primary-blue);
  text-transform: uppercase;
}

/******* TYPED TEXT  *******/
.typed-text {
  font-size: 2em;
  color: #00CACA;
  color: var(--primary-blue);
}

@media(max-width: 768px) {
  .main-info h1 {
    font-size: 1.7rem !important;
  }
  .typed-text { 
    font-size: 1.3rem !important;
  }
}

/******* HEADER MAIN OFFER BUTTON  *******/


.btn-main-offer{  
  border: 1px solid #ffffff;  
  border: 1px solid var(--primary-white);
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: #ffffff;
  color: var(--primary-white);
  margin: 2rem;
  text-decoration: none;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: #00CACA;
  background-color: var(--primary-blue);
  color: #ffffff;
  color: var(--primary-white);
  transition: .2s ease-out;
  cursor: pointer;
}

/******* HEADER MAIN OFFER BUTTON *******/
.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

@media(max-width: 768px) {
  .particles-canvas {
    display: none;
  }
}

/************************ ABOUT ME ************************/


.photo-wrap{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding-top: 12px;
}

.profile-img {
  width: 16rem;
  border-radius: 50%;
  padding: .4rem;
  border: 1px solid #00CACA;
  border: 1px solid var(--primary-blue);
}

.about-heading{
  color: #00CACA;
  color: var(--primary-blue);
  text-transform: uppercase;
}

@media(max-width: 468px) {
  .profile-img {
    width: 10rem;
  }
}

/************************ SERVICES ************************/

.services{
  text-align: center;
  padding-bottom: 2rem;
  background: #f4f4f4;
  background: var(--primary-light-grey);
}

.services h1 {
  color: #00CACA;
  color: var(--primary-blue);
  text-transform: uppercase;
}

.services .circle {
  position:relative;
  margin: 0.365rem auto;
  background: #00CACA;
  background: var(--primary-blue);
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
}

.services .icon {
  font-size: 1.4rem;
  color: #ffffff;
  color: var(--primary-white);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%); 
}

.services .box {
  height: 16rem;
  border-bottom: 0.3125rem solid #00CACA;
  border-bottom: 0.3125rem solid var(--primary-blue);
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background-color: #ffffff;
  background-color: var(--primary-white);
  transition: 0.3s ease-in-out;
}

.services .box:hover {
  background: #00CACA;
  background: var(--primary-blue);
  border-bottom: 0.3125rem solid #212529;
  border-bottom: 0.3125rem solid var(--primary-dark);
}

.services .box:hover .icon {
  color: #212529;
  color: var(--primary-dark);
}

.services .box:hover .circle {
  background: #ffffff;
  background: var(--primary-white);
}

.services .box:hover p {
  color: #ffffff;
  color: var(--primary-white);
}

/************************ EXPERIENCE ************************/

.experience {
  padding: 0.7rem;  
}

.experience-wrapper {
  position: relative;
  padding: 3.125rem;
  overflow: hidden;
}

.experience h1 {
  text-transform: uppercase;
  color: #00CACA;
  color: var(--primary-blue)
}

.experience-wrapper::before{
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: #ccd1d9;
  background: var(--primary-grey);
  z-index: 1;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  clear: both;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}

.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.3125rem solid #212529;
  border: 0.3125rem solid var(--primary-dark);
  background: #00CACA;
  background: var(--primary-blue);
  margin-top: 0.625rem;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  padding: 0 0.9375rem;
  color: var(--primary-dark-grey-grey);
  direction: ltr;
}

.timeline-content h3{
  margin-top: 0.3125rem;
  color: #ef4035;
  color: var(--primary-red);
  font-weight: 900;
}

/******* MEDIA *******/

@media(max-width: 768px) {
  .experience .container {
    padding-left: 0;
  }
  .experience-wrapper::before {
    left: 0.5rem;
    margin-bottom: 1.875rem;

  }
  .timeline-block-right {
    float: none;
    width: 100%;
  }
  .timeline-block-left {
    float: none;
    direction: ltr;
    width: 100%;
  }
}

/************************ PORTFOLIO ************************/

.portfolio-wrapper {
  background: #f4f4f4;
  background: var(--primary-light-grey);
  padding: 3rem 0;
}

.portfolio-wrapper h1 {
  color: #00CACA;
  color: var(--primary-blue);
}

.portfolio-image {
  width: 15rem;
  border: 1px solid #00CACA;
  border: 1px solid var(--primary-blue);
  padding: 0 0.5rem;
}

.portfolio-image-box{
  position: relative;
  margin: 1rem;
}

/******* OEVRFLOW BOX *******/

.overflow {
  position: absolute;
  opacity: 0;
  background: #000000;
  background: var(--primary-black);
  width: 15rem;
  height: 8rem;
  top: 0;
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  cursor: pointer;
}

.portfolio-image-box:hover .overflow {
  opacity: 60%;
  transition: 0.3s ease-in-out;
}

.portfolio-icon {
  position: absolute;
  opacity: 0;
  margin-left: -8rem;
  margin-top: 3.6rem;
  color: #00CACA;
  color: var(--primary-blue);
  font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon {
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

.popupbox-wrapper {
  margin-top: 7rem ;
}

/******* OEVRFLOW BOX  *******/

.portfolio-image-popupbox {
  width: 45rem;
  padding: 0 0.5rem;
}

.hyper-link{
  cursor: pointer;
  color: #00CACA;
  color: var(--primary-blue);
}

.hyper-link:hover{
  color: #ef4035;
  color: var(--primary-red);
}

/******* MEDIA  *******/

@media(max-width: 768px) {
  .portfolio-image-popupbox {
    width: 100%;
  } 
}

@media(max-height: 570px) {
  .popupbox-wrapper {
    height: 100%;
  }

  .portfolio-image-popupbox {
    width: 50%;
  } 
}

@media(max-height: 280px) {
  .popupbox-wrapper {
    height: 90%;
  }

  .portfolio-image-popupbox {
    width: 30%;
  } 
}

/************************ TESTIMONIALS ************************/
.testimonials {
  background-image: url(/portfolio-alexmo/static/media/testimonials-bg.75612be7.jpg);
  background-size: cover;
  background-color: #000000;
  background-color: var(--primary-black);
  text-align: center;
  padding-bottom: 4rem;
  margin-bottom: 0;
  width: 100%;
}

.testimonials h1 {
  color: #00CACA;
  color: var(--primary-blue);
  padding: 3rem 0;
  text-transform: uppercase;
}

.testimonials-content {
  text-align: center;
  max-width: 53.125rem;
  margin: 0 auto 2rem auto;
  background: rgba(0,0,0,00.7);
  background: var(--primary-transparent-black);
}

.myCarousel {
  color: #ffffff;
  color: var(--primary-white);
  background: transparent;
  margin-top: -6%;
  width: 54%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 6%;
  padding-bottom: 8%;
  padding-left: 5%;
  padding-right: 5%;
  height: 17.875rem;
}

.myCarousel h3 {
  color: #00CACA;
  color: var(--primary-blue);
  font-size: 600;
  text-transform: uppercase;
}

.myCarousel p {
  color: #ccd1d9;
  color: var(--primary-grey);
}

.carousel .slide img {
  width: 10rem !important;
  border-radius: 50%;
}

.carousel .slide {
  background: transparent !important;
  height: 100% !important;
  padding-top: 2rem;
}

.carousel-root {
  margin: auto !important;
  margin-top: 3% !important;
} 

/************************ CONTACTS  ************************/
.contacts {
  background: #212529;
  background: var(--primary-dark);
  padding: 3rem 0;
}

.contacts h1 {
  color: #00CACA;
  color: var(--primary-blue);
  padding: 1.3rem 0;
  text-transform: uppercase;
}

.contacts p {
  color: #f4f4f4;
  color: var(--primary-light-grey);
  font-size: 1.2rem;
}

/******* INPUTS *******/

.contacts input, .contacts textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize:  none;
  border: 0;
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
  border-radius: 0;
  color: #00CACA;
  color: var(--primary-blue);
  outline: 0 !important;
  box-shadow: 0 !important;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}

.contacts .container {
  padding: 4rem 0;
}

.contacts input:focus, .contacts textarea:focus {
  background: transparent;
  color: #00CACA;
  color: var(--primary-blue);
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
}

.contacts textarea {
  height: 12.5rem !important;
}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;
}

.line {
  position: relative;
  top: -0.8rem;
  width: 0;
  height: 0.125rem;
  background: #00CACA;
  background: var(--primary-blue);
  display: inline-block;
}

input:focus+.line, textarea:focus+.line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

input.form-control {
  margin: -0.3rem 0;
}

textarea.form-control {
  margin: -0.3rem 0;
}

.error-message {
  color: #ffffff;
  color: var(--primary-white);
  background: #f33501;
  background: var(--primary-hover-red);
  text-align: left;
}

.succuess-message {
  font-weight: 900;
  color: green;
  color: var(--primary-green);
}

/******* MEDIA *******/

@media(max-width: 768px) {
  .contacts {
    overflow-x: hidden;
  }
  
  .contacts .container {
    padding: 0 1rem;
  }

  .contacts p {
    padding: 0 0.4rem;
  }
}

/************************ FOOTER  ************************/
.footer {
  background: #000000;
  background: var(--primary-black);
  color: #666666;
  color: var(--primary-dark-grey);
  padding: 3rem;
}

.footer a{
  text-decoration: none;
  color: #666666;
  color: var(--primary-dark-grey);
}

.footer a:hover {
  -webkit-text-emphasis: none;
          text-emphasis: none;
  color: #00CACA;
  color: var(--primary-blue);
  cursor: pointer;
}

.footer p {
  margin-bottom: 0;
}

